export async function postNewOrder(dispatch, newOrderObject, successHandler) {
  const request = axios({
    method: 'post',
    url: Endpoints.sushiOrderList(),
    data: newOrderObject,
  }).then((response) => {
    successHandler && successHandler(response);
  })
    .catch((error) => {
      if (error.response && error.response.data) {
        console.log(error);
        window.alert(error.response.data.message);
      }
    });
}
