import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Store } from '../../store';
import {
  addPresetToCart, removeItemFromCart, setPickUpTime, updateMessage, fetchOrderList, deleteOrderById,
} from '../../actions/action-sushi';
import OrderListDisplay from './order-list-display';

const useStyles = makeStyles((theme) => ({
  displayContainer: {
    width: '95%',
    margin: '50px auto',
  },
}));

export default function OrderListIndex() {
  const { state, dispatch } = React.useContext(Store);
  const classes = useStyles();

  React.useEffect(() => {
    state.orderList.length < 1 && fetchOrderList(dispatch);
  }, []);

  return (
    <>
      {
        state.orderList && (
          state.orderList.map((orderListItem, i) => (
            <OrderListDisplay orderList={orderListItem} key={i} deleteOrderMethod={(id) => { deleteOrderById(dispatch, id); }} />
          ))
        )
      }
    </>
  );
}
