const useStyles = makeStyles((theme) => ({
  receiptHeader: {
    color: '#fff',
    background: '#337ab7',
    padding: '10px',
  },
  card: {
    display: 'inline-block',
    width: '100%',
    border: '1px solid #337ab7',
    borderRadius: '5px',
  },
  orderItemName: {
    maxWidth: '70%',
    float: 'left',
  },
  orderItemPrice: {
    float: 'right',
    textAlign: 'right',
    width: '30%',
  },
  orderItem: {
    width: '100%',
    overflow: 'auto',
    padding: '10px',
  },
  hrLine: {
    border: '0.3px solid #c3c3c3',
    width: '97%',
  },
}));

export default function OrderListDisplay(props) {
  const { orderList, deleteOrderMethod } = props;
  const classes = useStyles();

  const options = {
    weekday: 'long', year: 'numeric', month: 'long', day: 'numeric', hour: '2-digit', minute: '2-digit', second: '2-digit',
  };

  function countTotalPrice(order) {
    let totalPriceCounter = 0;
    order.map((item) => {
      totalPriceCounter += (item.presetObject.price * item.orderNumber);
    });
    setTotalPrice(totalPriceCounter);
  }
  function renderOrder(orderItem, i) {
    const pickUpTime = new Date(orderItem.pickUpTime);
    let totalPriceCounter = 0;

    return (
      <div className={classes.card} key={i}>
        <h4 className={classes.receiptHeader}>{pickUpTime.toLocaleString('fi-FI', options)}</h4>
        {
          orderItem.length == 0 && (
            <div className={classes.orderItem}>No items in cart</div>
          )
        }
        {
          orderItem && (
            <div>
              {
                orderItem.order.map((item, i) => {
                  totalPriceCounter += item.presetObject.price * item.orderNumber;
                  return (
                    <div className={classes.orderItem} key={i}>
                      <div className={classes.orderItemName}>{`${item.orderNumber} x ${item.presetObject.name}`}</div>
                      <div className={classes.orderItemPrice}>{`${(item.presetObject.price * item.orderNumber).toFixed(2)} €`}</div>
                    </div>
                  );
                })
              }
              <hr className={classes.hrLine} />
              <div className={classes.orderItem}>
                Kokonaissumma:
                <h5>{`${totalPriceCounter.toFixed(2)} €`}</h5>
              </div>
              <div className={classes.orderItem}>
                Pick up time:
                <h5>{pickUpTime.toLocaleString('fi-FI', options)}</h5>
              </div>
              <div className={classes.orderItem}>
                Message:
                <h5>{orderItem.message}</h5>
              </div>
              <Button fullWidth onClick={() => { deleteOrderMethod(orderItem._id); }}> Delete Order </Button>
            </div>
          )
        }
      </div>
    );
  }
  return (
    <div className="">
      {console.log(orderList)}
      {
        orderList && (
          orderList.map((order, i) => renderOrder(order, i))
        )
      }
    </div>
  );
}
