import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import ButtonBase from '@material-ui/core/ButtonBase';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import DeleteIcon from '@material-ui/icons/Delete';
import MomentUtils from '@date-io/moment'; import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import ThumbnailImageCrop from '../image_components/thumbnail-image-crop';
import { capitalizeFirstLetter } from '../../assets/helpers/helper-functions-index';


const useStyles = makeStyles((theme) => ({
  displayContainer: {
    width: '95%',
    margin: '20px auto',
    display: 'inline',
    flexWrap: 'wrap',
  },
  textField: {
    margin: '10px 0 20px',
    width: '100%',
  },
}));

export default function TimeSelectPanel(props) {
  const {
    itemsInCart, pickUpTime, setPickUpTimeMethod, updateMessageMethod, message,
  } = props;
  const classes = useStyles();

  function formatDateString(date) {
    const day = date.getDate();
    const dd = date.getDate();
    const mm = date.getMonth();
    const yyyy = date.getFullYear();
    const hours = date.getHours();
    const mins = date.getMinutes();
    const dateTimeString = `${day} ${yyyy}-${mm}-${dd}T:${hours}0:${mins}:00`;
    return (dateTimeString);
  }
  const options = {
    weekday: 'short', year: 'numeric', month: 'long', day: 'numeric', hour: '2-digit', minute: '2-digit', second: '2-digit',
  };

  const handleDateChange = (date) => {
    setPickUpTimeMethod(new Date(date));
  };

  return (
    <div className={classes.displayContainer}>
      <h4>2. Valitse toimitusapa </h4>
      {
        capitalizeFirstLetter(pickUpTime.toLocaleString('fi-FI', options))
      }
      <MuiPickersUtilsProvider utils={MomentUtils}>
        <Grid container justify="space-around">
          <KeyboardDatePicker
            disableToolbar
            variant="inline"
            format="DD/MM/YYYY"
            margin="normal"
            id="date-picker"
            label="Select Date"
            value={pickUpTime}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change date',
            }}
          />
          <KeyboardTimePicker
            margin="normal"
            id="time-picker"
            label="Select Time"
            value={pickUpTime}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change time',
            }}
          />
        </Grid>
        <p>Additional Info</p>
        <TextField
          id="outlined-multiline-static"
          label="Message"
          multiline
          rows="5"
          className={classes.textField}
          value={message}
          onChange={(e) => { updateMessageMethod(e.target.value); }}
          margin="normal"
          variant="outlined"
        />
      </MuiPickersUtilsProvider>
    </div>
  );
}
